@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@100;300;400;500;600;700&display=swap");

* {
  font-family: "Poppins", sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  text-decoration: none;
  outline: none;
  border: none;
  list-style: none;
}
h1 {
  font-family: sans-serif;
}
$crimson: #5fded7;
$black: #171D1C;
$light-bg: #1a1a1a;
$white: #fff;
$light-color: #aaa;
$box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.7);

@mixin grid($val) {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax($val, 1fr));
  gap: 1.5rem;
}
html {
  font-size: 65.5%;
  &::-webkit-scrollbar {
    width: 1rem;
  }
  &::-webkit-scrollbar-track {
    background: $light-bg;
  }
  &::-webkit-scrollbar-thumb {
    background: $crimson;
    border-radius: 5rem;
    
  }
}
body {
  background: $black;
  // background: url(../assets/images/ocean2.jpg);
  padding-top: 80px;
  // height: 100%;
}
section {
  padding: 7rem 9%;
  padding-top: 15rem;
}
.heading {
  text-align: center;
  font-size: 6vw;
  color: $white;
  margin-bottom: 3rem;
  text-transform: uppercase;
  span {
    text-transform: uppercase;
    color: $crimson;
  }
}
.btn {
  display: inline-block;
  margin-top: 1rem;
  transition: 2s ease-in-out;
  border-radius: 10px;
  background: {
    image: linear-gradient(45deg, #5fded7 50%, #436969 50%);
    position: 100%;
    size: 400%;
  }
  &:hover {
    background-position: 0;
  }
  padding: 0.7rem;
  font-size: 1.7rem;
  color: $white;
  padding-left: 2rem;
  transition: 0.3s linear;
  &:hover {
    background-color: $black;
    color: $black;
  }
  i {
    height: 4rem;
    width: 4rem;
    line-height: 4rem;
    font-size: 1.7rem;
    text-align: center;
    background: $white;
    color: #436969;
    border-radius: 50%;
    margin-left: 1rem;
    transition: 0.2s linear;
  }
}
.navbar {
  background-color: $black;
  position: fixed;
  width: 100%;
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.5rem;
  top: 0;
  z-index: 20;
  
  .nav-container {
    
    display: flex;
    justify-content: center;
    align-items: center; 
    width: 100%;
    height: 80px;
    max-width: 1500px;
    a img {
      border-radius: 5px;
      width: 7.2rem;
      height: auto;
      object-fit: cover;
    }
    
  }
  .nav-logo {
    display: flex;
    align-items: center;
    margin-left: 20px;
    cursor: pointer;
    font-size: 2rem;
    flex-grow: 1;
  }
  .nav-menu {
    display: flex;
    text-align: center;
    margin-right: 2rem;
  }
  .nav-links {
    color: $white;
    font-weight: 500;
    text-decoration: none;
    font-size: 1.6rem;
    padding: 1rem;
    margin-left: 8rem;
    height: 100%;
    font-family: sans-serif;
    
    
    
    
    
    &:hover {
      color: #5fded7;
      font-weight: 500;  
    }
  }
  .nav-item .active
  {
    font-size: 1.6rem;
    padding: 1rem;
    margin-left: 8rem;
    height: 100%;
    font-family: sans-serif;
    color: #5fded7;
    border-bottom: solid 2px #5fded7;
    font-weight: 500;
    border-radius: 10px;
  }
  
  .nav-icon {
    display: none;
  }
}
@media screen and (max-width: 960px) {
  .navbar {
    .nav-menu {
      display: flex;
      flex-direction: column;
      width: 100%;
      position: absolute;
      top: 80px;
      left: -110%;
      padding: 1rem;
      opacity: 1;
      transition: all 0.5s ease;
      
      &.active {
        background: $black;
        left: 0px;
        opacity: 1;
        transition: all 0.5s ease;
        z-index: 1;
        ;
      }
    }
    .nav-item .active {
      margin-left: 0;
      margin-bottom: 15px;
      padding: 1.5rem;
      width: 100%;
      display: table;
      color: $crimson;
      border: none;
      border-bottom: solid 1px $crimson;
      border-radius: 0;
    }
    .nav-links {
      margin-left: 0;
      margin-bottom: 15px;
      padding: 1.5rem;
      width: 100%;
      display: table;
      border-bottom: solid 1px rgb(188, 188, 188);
    }
    .nav-icon {
      display: block;
      position: absolute;
      top: 0px;
      right: 25px;
      transform: translate(-100%, 60%);
      font-size: 3rem;
      cursor: pointer;
      color: $crimson;
    }
  }
}
.home {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  gap: 4rem;
  min-height: calc(100vh - 6.5rem);
  .image {
    flex: 1 1 42rem;
    text-align: center;
    margin: 0;

    img {
      width: 100%;
      max-width: 65em;
      object-fit: cover;
      transition: 0.8s;
      &:hover {
        transform: rotate(-30deg)
      }
    }
  }

  .content {
    flex: 1 1 42rem;

    a {
      margin-left: 2rem;
      margin-top: 5rem;
    }

    h1 {
      color: $white;
      font-family: sans-serif;
      font-size: 6rem;
      padding-bottom: 0.5rem;
    }
    .span {
      font-size: 4rem;
      color: #5fded7;
      padding: 0.5rem 0;
      display: block;
    }
    .span2 {
      font-size: 4rem;
      color: $white;
      padding: 0.5rem 0;
      display: block;
    }

    p {
      font-size: 1.6rem;
      color: $light-color;
      padding: 1rem 0;
      line-height: 2;
    }
  }
}

.about {
  h1 {
    font-size: 6rem;
  }
  a {
    padding-left: 1rem;
  }

  .row {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    gap: 2rem;

    .info-container {
      flex: 1 1 42rem;

      h1 {
        font-size: 3rem;
        color: $white;
        text-transform: uppercase;
        padding-bottom: 1rem;
      }

      .box-container {
        padding: 1rem 0;
        display: flex;
        flex-wrap: wrap;
        gap: 2rem;

        .box {
          flex: 1 1 20rem;

          h2 {
            font-size: 1.7rem;
            color: $white;
            padding: 1rem 0;
            font-weight: normal;

            span {
              color: $light-color;
              font-weight: 500;
            }
          }
        }
      }
    }

    .count-container {
      flex: 1 1 42rem;
      display: flex;
      flex-wrap: wrap;
      gap: 2rem;

      .box {
        flex: 1 1 20rem;
        padding: 3rem 4rem;
        border-radius: 0.5rem;
        background: $light-bg;
        box-shadow: $box-shadow;
        transition: 0.3s;
        border-top: solid 1px #5fded7;
        &:hover {
          transform: scale(1.05);
        }

        h2 {
          font-size: 5rem;
          color: $crimson;
        }

        p {
          font-size: 2rem;
          color: $light-color;
        }
      }
    }
  }
}

.skills {
  h1 {
    font-size: 6rem;
  }
  .box-container {
    @include grid(16rem);

    .box {
      padding: 2rem;
      text-align: center;
      box-shadow: $box-shadow;
      border-radius: 0.5rem;
      background: $light-bg;
      transition: 0.4s;
      border-top: solid 1px $crimson;

      &:hover {
        background: $white;

        h2 {
          color: $black;
        }
      }

      img {
        height: 7rem;
        margin-bottom: 1rem;
        color: $crimson;
      }

      h2 {
        color: $white;
        font-size: 1.7rem;
        font-weight: normal;
        text-transform: uppercase;
      }
    }
  }
}

.education {
  h1 {
    font-size: 6rem;
  }
  .box-container {
    display: flex;
    flex-wrap: wrap;

    .box {
      flex: 1 1 40rem;
      margin-left: 2rem;
      padding-top: 0;
      padding-bottom: 0;
      padding-left: 3.5rem;
      padding-bottom: 3rem;
      border-left: 0.1rem solid $light-color;
      position: relative;

      i {
        height: 4.5rem;
        width: 4.5rem;
        line-height: 4.5rem;
        font-size: 1.7rem;
        border-radius: 50%;
        background: $crimson;
        color: $white;
        text-align: center;
        position: absolute;
        top: -1rem;
        left: -2.5rem;
      }

      span {
        font-size: 1.7rem;
        color: $white;
        border-radius: 5rem;
        padding: 0.5rem 1.5rem;
        background: $light-bg;
      }

      h2 {
        font-size: 2rem;
        padding: 1rem 0;
        color: $white;
        padding-top: 3rem;
        font-weight: normal;
      }

      p {
        font-size: 1.5rem;
        color: $white;
        line-height: 2;
      }
    }
  }
}

.portfolio {
  text-align: center;
  height: 100%;
  h1 {
    font-size: 6rem;
  }
  .box-container {
    @include grid(31rem);
    margin-bottom: 2.5rem;

    .box {
      height: 30rem;
      overflow: hidden;
      position: relative;
      border: solid 1.3px $crimson;

      &:hover {
        .content {
          transform: translateY(0%);
        }
      }

      img {
        height: 100%;
        width: 100%;
        object-fit: cover;
      }
      .kanap {
        height: 123.4%;
      }

      .content {
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
        background: $light-bg;
        display: flex;
        flex-flow: column;
        align-items: center;
        justify-content: center;
        padding: 2rem;
        transform: translateY(-100%);
        transition: 0.3s linear;

        h2 {
          font-size: 2rem;
          color: $white;
          text-transform: uppercase;
          font-weight: normal;
        }

        p {
          padding: 1rem 0;
          font-size: 1.5rem;
          line-height: 1.5;
          color: $light-color;
        }

        a {
          font-size: 2rem;
          color: $crimson;

          &:hover {
            color: $white;
          }
        }
      }
    }
  }
}

.contact {
  padding-bottom: 25rem;
  .mail {
    color: $crimson;
    &:hover {
      border-bottom: solid 1px $crimson;
    }
  }
  #btn2 {
    width: 12rem;
    padding: 1rem;
    background: #436969;
    // border: 1px solid $crimson;
    border-radius: 10px;
    color: $white;
    font-weight: 800;
    font-size: 1.5rem;
    transition: 0.5s ease-in-out;
    &:hover {
      background: $crimson;
      color: $black;
    }
  }

  h1 {
    font-size: 6rem;
  }
  .row {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    gap: 2rem;

    .info-container {
      width: 40rem;

      h1 {
        font-size: 3rem;
        text-transform: uppercase;
        color: $white;
        padding-bottom: 1rem;
      }

      p {
        font-size: 1.5rem;
        line-height: 2;
        color: $light-color;
        padding: 1rem 0;
      }

      .box-container {
        padding: 2rem 0;

        .box {
          display: flex;
          gap: 2rem;
          padding: 1rem 0;

          i {
            font-size: 3rem;
            color: $crimson;
          }

          h2 {
            font-size: 2rem;
            color: $white;
          }
        }
      }

      .share {
        a {
          font-size: 2rem;
          height: 4.5rem;
          width: 4.5rem;
          line-height: 4.5rem;
          border-radius: 50%;
          background: $light-bg;
          color: $white;
          text-align: center;
          margin-right: 0.3rem;

          &:hover {
            background: $crimson;
          }
        }
      }
    }
    form {
      flex: 1 1 42rem;

      .inputBox {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;

        input {
          width: 49%;
        }
      }

      .inputBox input,
      textarea  {
        border-radius: 5rem;
        padding: 1.2rem 1.8rem;
        font-size: 1.6rem;
        color: $white!important;
        text-transform: none;
        margin: 0.7rem 0;
        background: $light-bg;
        
        
      }

      textarea {
        width: 100%;
        border-radius: 1rem;
        resize: none;
        height: 25rem;
      }

      .btn {
        padding: 1rem 3rem;
        cursor: pointer;

        &:hover {
          background: $white;
          color: $black;
        }
      }
    }
  }
}
// media queries

@media (max-width: 1200px) {
  html {
    font-size: 55%;
  }

  section {
    padding: 3rem 2rem;
  }
}
@media (min-width: 880px) and (max-width: 1200px) {
  .portfolio .box-container {
    margin-bottom: 15rem;
  }
}

@media (min-width: 775px) and (max-width: 1200px) {
  .contact {
    padding-bottom: 35rem;
  }
}

@media (max-width: 768px) {
  .heading {
    font-size: 10vw !important;
  }

  .home {
    text-align: center;
    gap: 2rem;

    .content {
      h3 {
        font-size: 3rem;
      }

      span {
        font-size: 2.5rem;
      }
    }
  }
  .contact .row form .inputBox input {
    width: 100%;
    margin-bottom: 1rem;
  }
}

@media (max-width: 450px) {
  html {
    font-size: 50%;
  }
} 

.span3 {
  color: $white;
  font-size: 4rem;
  color: $white;
  padding: 0.5rem 0;
  display: block;
}

html,
body {
  width: 100%;
  overflow-x: hidden;
}

.mouse_scroll {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  width: 100%;
  height: 100%;
  padding-bottom: 10px;
  margin-top: 5rem;
  padding-bottom: 10rem;
}

.m_scroll_arrows {
  display: block;
  width: 5px;
  height: 5px;
  -ms-transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
  border-right: 2px solid $crimson;
  border-bottom: 2px solid $crimson;
  width: 16px;
  height: 16px;
}

.unu {
  margin-top: 1px;
}

.unu,
.doi,
.trei {
  -webkit-animation: mouse-scroll 1s infinite;
  -moz-animation: mouse-scroll 1s infinite;
  animation: mouse-scroll 1s infinite;
}

.unu {
  -webkit-animation-delay: 0.1s;
  -moz-animation-delay: 0.1s;
  -webkit-animation-direction: alternate;

  animation-direction: alternate;
  animation-delay: alternate;
}

.doi {
  -webkit-animation-delay: 0.2s;
  -moz-animation-delay: 0.2s;
  -webkit-animation-direction: alternate;

  animation-delay: 0.2s;
  animation-direction: alternate;

  margin-top: -6px;
}

.trei {
  -webkit-animation-delay: 0.3s;
  -moz-animation-delay: 0.3s;
  -webkit-animation-direction: alternate;

  animation-delay: 0.3s;
  animation-direction: alternate;

  margin-top: -6px;
}

.mouse {
  height: 42px;
  width: 24px;
  border-radius: 14px;
  transform: none;
  border: 2px solid $crimson;
  top: 170px;
}

.wheel {
  height: 5px;
  width: 2px;
  display: block;
  margin: 5px auto;
  background: $crimson;
  position: relative;
  height: 4px;
  width: 4px;
  border: 2px solid $crimson;
  -webkit-border-radius: 8px;
  border-radius: 8px;
}
.wheel {
  -webkit-animation: mouse-wheel 0.6s linear infinite;
  -moz-animation: mouse-wheel 0.6s linear infinite;
  animation: mouse-wheel 0.6s linear infinite;
}
@-webkit-keyframes mouse-wheel {
  0% {
    opacity: 1;
    -webkit-transform: translateY(0);
    -ms-transform: translateY(0);
    transform: translateY(0);
  }
  100% {
    opacity: 0;
    -webkit-transform: translateY(6px);
    -ms-transform: translateY(6px);
    transform: translateY(6px);
  }
}
@-moz-keyframes mouse-wheel {
  0% {
    top: 1px;
  }
  25% {
    top: 2px;
  }
  50% {
    top: 3px;
  }
  75% {
    top: 2px;
  }
  100% {
    top: 1px;
  }
}
@keyframes mouse-wheel {
  0% {
    top: 1px;
  }
  25% {
    top: 2px;
  }
  50% {
    top: 3px;
  }
  75% {
    top: 2px;
  }
  100% {
    top: 1px;
  }
}
@-webkit-keyframes mouse-scroll {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 0.5;
  }
  100% {
    opacity: 1;
  }
}
@-moz-keyframes mouse-scroll {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 0.5;
  }
  100% {
    opacity: 1;
  }
}
@keyframes mouse-scroll {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 0.5;
  }
  100% {
    opacity: 1;
  }
}



